.header {
    width: 100%;
    height: 57px;
    display: flex;
    align-items: center;
    position: relative;

    & h1 {
        color: #d9e9ed;
        font-size: 24px;
    }

    & .action_btn {
        border: 1px solid #1d252f;
        border-radius: 4px;
        height: 34px;
        width: 90.6px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        right: 0;
        position: absolute;

        & span {
            font-size: 14px;
            color: #d5d5d5;
            margin-right: 4px;
        }

        & .icon {
            font-size: 14px;
            color: #fff;
        }
    }
}

.box {
    display: flex;
    align-items: center;

    & span {
        font-size: 14px;
        color: #8a8d98;
        margin-right: 4px;
    }
}