@import "../../styles/lavenst.scss";

.list {
    & .title {
        text-transform: none;
        color: #d9e8ed;
        font-size: 24px;
        line-height: 32px;
        font-weight: 700;
        margin-bottom: 10px;
        display: block;
    }

    &.discovery {
        padding-top: 25px;
        padding-bottom: 16px; 

        & .container {
            width: 100%;
            display: flex;
            overflow: auto;
            scroll-snap-type: x mandatory;
            flex-direction: row;
            flex-wrap: nowrap;
        }
    }

    &.apps_filter {
        height: 48px;

        & .container {
            display: flex;
            overflow: auto;
            flex-direction: row;
            align-items: center;
        }
    }

    &.title_grid {
        display: grid;
        grid-template-columns: repeat(3,1fr);
        gap: 24px;
    }

    &.row {
        display: flex;
        flex-direction: column;
    }

    &.box {
        display: grid;
        grid-template-columns: repeat(auto-fill,minmax(170px,1fr));
        grid-gap: 15px;
    }
}