h4.custom_header {
    color: #fff!important;
    white-space: nowrap;
    font-size: 24px;
    font-weight: 700;

    &::after {
        content: "";
        width: 40px;
        transform: scaleX(1);
        display: block;
        border-bottom: 3px solid #fbc500;
        transform-origin: 0 50%;
    }
}

.message {
    color: #fff;
    font-weight: 600;
    margin: 0;
    font-size: 16px;
    line-height: 1.5;
}

.tab_controller_container {
    margin: 8px auto -16px;
    padding: 0;
    overflow-x: auto;
    scrollbar-width: none;
    -webkit-overflow-scrolling: touch;
    -ms-overflow-style: -ms-autohiding-scrollbar;
    display: flex;
    flex-direction: row;
    width: 100%;

    & .tab {
        margin-right: 16px;
        display: flex;
        flex-direction: row;
        align-items: center;
        outline: none;
        padding: 9px 10px;
        border-radius: 8px;
        border: 1px solid #10161d;
        background-color: transparent;
        transition: background-color .1s ease-in-out,border-color .1s ease-in-out;
        cursor: pointer;

        &:hover {
            border-color: #657182;
        }

        &.active {
            background-color: #1c252f;
            border-color: #1c252f;
            outline: none;
        }

        & span {
            font-weight: 700;
            font-size: 13px;
            color: #fff;
            white-space: nowrap;
        }

        & .icon {
            margin-right: 6px;
            color: #fff;
            font-size: 13px;
        }
    }
}

.options_container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    box-sizing: border-box;
    margin: 24px auto 8px;

    & .options-left{ 
        display: flex;
        align-items: center;
        max-width: 70vw;
        box-sizing: border-box;
    }

    & .options-right {
        display: flex;
        align-items: center;
        text-align: right;
        box-sizing: border-box;
    }

    & span.option {
        color: #8a8d98;
        margin: 8px 8px 8px 0;
        font-size: 14px;
        display: flex;
        align-items: center;
        justify-content: center;

        &.action {
            cursor: pointer;

            &:hover {
                color: #d5d5d5!important;
            }
    
        }

        & .icon {
            margin-top: 2px;
            margin-left: 4px;
        }
    }   

    & .option_box {
        display: flex;
        box-sizing: border-box;
    }

    & .option_btn {
        display: flex;
        align-items: center;
        justify-content: center;
        white-space: nowrap;
        border-radius: 4px;
        background-color: transparent;
        transition: background-color .1s linear;
        cursor: pointer;
        width: 35px;
        height: 35px;

        &.active {
            background-color:#434f5c;
            border: 0;

            & .icon {
                color: #fff;
            }
        }

        &:hover {
            & .icon {
                color: #fff;
            }
        }

        & .icon {
            font-size: 15px;
            color: #8a8d98;
        }
    }

    & .option_chip_btn {
        display: block;
        font-weight: 400;
        font-size: 15px;
        padding: 5px 10px;
        cursor: pointer;
        margin-left: 24px;

        & .icon {
            color: #8a8d98;
            font-size: 15px;
            margin-right: 4px;
        }

        & span {
            color: #8a8d98;
            font-size: 15px;
        }

        &:hover {
            & .icon {
                color: #fff;
            }

            & span {
                color: #fff;
            }
        }
    }
}