.modal {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    left:0;
    z-index: 999;
    background-color: rgba(0,0,0,.4);

    & .container {

    }

    &.apps_filter {
        & .container {
            width: 600px;
            height: 600px;
            padding: 30px 40px;
            box-sizing: border-box;
            box-shadow: 0 28px 48px rgba(0, 0, 0, 0.4);
            background-color: #060e18;
            border-radius: 8px;

            & .header {
                display: flex;
                margin-bottom: 24px;

                & .group {
                    flex: 1;
                    display: flex;
                    flex-direction: column;

                    & .title {
                        font-size: 20px;
                        color: #d5d5d5;
                    }

                    & .subtitle {
                        font-size: 14px;
                        color: #5e6b76;
                        margin-top: 4px;
                    }
                }

                & .action_btn {
                    height: 100%;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    margin-top: 12px;

                    & span {
                        color: #78a6b8;
                        font-size: 14px;

                        &:hover {
                            color: #fff;
                        }
                    }
                }
            }

            & .list {
                height: calc(100% - 64px);
                overflow-y: auto;

                & .title {
                    margin-top: 24px;
                    padding-bottom: 10px;
                    font-size: 16px;
                    line-height: 1.5;
                    color: #8a8d98;
                    font-weight: 400;
                }

                & .action_group {
                    display: flex;
                    flex-direction: row;
                    margin-left: -10px;
                    margin-bottom: 20px;

                    & .action_btn {
                        cursor: pointer;

                        & span {
                            display: block;
                            font-weight: 400;
                            font-size: 15px;
                            padding: 5px 10px;
                            color: #8a8d98;
                            text-overflow: ellipsis;
                            overflow: hidden;

                            &:hover {
                                color: #fff;
                            }
                        }
                    }
                }

                & .item {
                    display: flex;
                    flex-direction: row;
                    align-items: center;
                    width: 100%;
                    background-color: #10161d;
                    padding: 5px 10px;
                    margin-bottom: 12px;
                    border-radius: 16px;
                    cursor: pointer;
                    position: relative;
                    height: 65px;
                    box-sizing: border-box;

                    &:hover {
                        background-color: #19222d;
                    }

                    & span {
                        color: #fff;
                        font-size: 16px;
                        margin-left: 10px;
                    }

                    & input {
                        position: absolute;
                        right: 20px;
                    }
                }
            }
        }
    }

    &.movies_filter {
        position: absolute;
        width: 100%;
        height: 100%;
        opacity: 0;
        visibility: hidden;

        &.show {
            opacity: 1;
            visibility: visible;
        }

        & .menu_container {
            inset: 0px auto 0px 0px;
            //transform: translate3d(-9999px, 0px, 0px);
            position: absolute;
            flex-direction: column;
            justify-content: space-between;
            width: 500px;
            min-width: 320px;
            max-width: auto;
            height: 100%;
            background-color: #060d17;
            padding: 0 20px;
            box-sizing: border-box;
            overflow: auto;
            padding-bottom: 24px;
            left: -500px;
            transition: all .250s linear;

            &.show {
                left: 0;
            }

            & .header {
                width: 100%;
                height: 56px;
                display: flex;
                align-items: center;
                position: relative;

                & span.title {
                    font-size: 20px;
                    font-weight: 500;
                    letter-spacing: 0.0125em;
                    color: #d5d5d5;
                }

                & .action_btn {
                    justify-self: flex-end;
                    color: #78a6b8;
                    font-size: 14px;
                    position: absolute;
                    right: 0;
                    cursor: pointer;

                    &:hover {
                        color: #fff;
                    }
                }
            }

            & .title_container {
                width: 100%;
                height: 56px;
                display: flex;
                align-items: center;
                position: relative;
                border-top: 1px solid #11171d;

                & h5.title {
                    margin: 0;
                    font-size: 20px;
                    color: #d5d5d5;
                    font-weight: 500;
                }   

                & span.action_btn {
                    font-size: 14px;
                    color: #4c5a67;
                    position: absolute;
                    right: 0;
                    font-weight: 600;
                    cursor: pointer;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    & .icon {
                        font-size: 16px;
                    }
                }
            }

            & .box {
                & .sub_header {
                    width: 100%;
                    margin-bottom: 10px;

                    & span.title {
                        color: #d5d5d5;
                        font-size: 14px;
                    }
                }

                & .slider_container {
                    align-items: center;
                    display: flex;
                    justify-content: center;
                    height: 50px;
                    margin-bottom: 10px;
                    
                    & .label {
                        font-size: 12px;
                        color: #d5d5d5;
                    }

                    & .slider {
                        margin: 0 14px;
                        & .MuiSlider-track {
                            background-color: #4c5a68;
                        }
    
                        & .MuiSlider-thumb {
                            background-color: #4c5a68;
    
                            &:hover {
                                box-shadow: 0px 0px 0px 8px #4c5a6829;
                            }
                        }
    
                        & .PrivateValueLabel-circle-4 {
                            background-color: #4c5a68;
                        }
    
                        & .MuiSlider-rail {
                            background-color: #4c5a6899;
                        }
                    }
                }

                & .options_container {
                    display: flex;
                    justify-content: unset;
                    margin: 0;

                    & .col {
                        flex: 1;
                        display: flex;
                        align-items: flex-start;
                        flex-direction: column;

                        &:first-child {
                            margin-right: 7.5px;
                        }

                        &:last-child {
                            margin-left: 7.5px;
                        }

                        & .chip-btn {
                            flex: 1;
                            width:100%;
                            justify-content: flex-start;
                        }
                    }
                }

                & .chip-btn {
                    margin-right: 10px;
                    white-space: nowrap;
                    border: 1px solid transparent;
                    border-radius: 4px;
                    background-color: transparent;
                    transition: background-color .1s linear;
                    margin-bottom: 10px;
                    box-sizing: border-box;
                    font-size: 15px;
                    padding: 5px 10px;
                    cursor: pointer;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    & .icon {
                        color: #2b3440;
                        font-size: 15px;
                        margin-right: 4px;
                    }

                    & span {
                        color: #8a8d98;
                        font-size: 15px;
                        font-weight: 400;
                    }

                    &.active {
                        background-color: #434f5c;
                        & .icon {
                            color: #fff!important;
                        }

                        & span {
                            color: #fff!important;
                        }
                    }

                    &:hover {
                        & .icon {
                            color: #384454;
                        }

                        & span {
                            color: #d5d5d5;
                        }
                    }
                }
            }
        }
    }
}