@import "../../styles/lavenst.scss";

.backdrop {
    width: 100%;
    height: 450px;
    background-color: #000;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top center;
}

.info_container {
    width: 100%;
    max-width: 1170px;
    padding: 25px;
    border-radius: 16px;
    z-index: 99;
    min-height: 800px;
    margin: -75px auto;
    margin-bottom: 0;
    box-sizing: border-box;
    background-color: #060e18;
    display: flex;

    & h1.title {
        margin-top: 0;
        font-size: 28px;
        line-height: 1.2;
        color: #fff;

        & span.year {
            font-size: 20px;
            color: #8a8d98;
            text-shadow: 0 1px 0 #000;
        }
    }

    & .title_block {
        position: relative;
        display: flex;
        align-items: center;
    
        & .share_group {
            position: absolute;
            right: 0;

            & .btn {
                font-size: 15px;
                color: #4c5a68;   
                cursor: pointer;
                margin-left: 20px;

                &:hover {
                    color: #fff;
                }
            }
        }
    }

    & h4.title {
        font-size: 16px;
        color: #4c5a67;
        font-weight: 700;
        margin-bottom: 5px;
        margin-top: 24px;
        display: block;
    }

    & .text {
        white-space: pre-line;
        font-size: 16px;
        line-height: 1.5;
        color: #8a8d98;
        margin: 0;
    }

    & .col-left {
        width: 33.3333333333%;
        box-sizing: border-box;

        & .poster {
            padding-top: 142.1686747%;
            background-color: #fff;
            border-radius: 5px 5px 0 0;
            background-position: center center;
            background-repeat: no-repeat;
            background-size: cover;
        }

        & .action_container {
            display: flex;
            background-color: #1d252f;
            border-radius: 0 0 5px 5px;

            & .item {
                flex: 1;
                border-radius: 4px;
                display: flex;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                cursor: pointer;

                & .icon {
                    margin-bottom: 2px;
                    font-size: 14px;
                    margin: 15px auto 5px auto;
                    color: #d9e8ed;
                }

                & span {
                    font-size: 13px;
                    font-weight: 400;
                    height: 28px;
                    letter-spacing: .4px;
                    line-height: 26.4px;
                    color: #d9e8ed;
                    padding-bottom: 10px;
                }

                &.active {
                    & .icon, & span {
                        color: #fbc500;
                    }
                }

                &:hover {
                    background-color: #252d37;
                }
            }
        }

        & .information_group {
            & .clearfix {
                margin-bottom: 6px;
                display: flex;

                & .title {
                    display: block;
                    width: 120px;
                    font-weight: 700;
                    font-size: 16px;
                    margin-bottom: 5px;
                    text-transform: uppercase;
                    color: #4c5a67;
                }

                & .value {
                    color: #8a8d98;
                    font-size: 14px;

                    &.linking {
                        color: #78a6b8;
                    }
                }
            }
        }
    }

    & .col-right {
        flex: 1;
        width: 66.6666666667%;
        padding: 0 15px;
        padding-left: 30px;
        box-sizing: border-box;

        & .apps_supported_group {
            & .tab_container {
                & .tab_header{
                    height: 41px;
                    background-color: #0a151f;
                    display: flex;
                    border-radius: 4px;
                    padding: 4px;
                    box-sizing: border-box;

                    & .item {
                        display: block;
                        padding: 5px 15px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        border-radius: 4px;
                        cursor: pointer;

                        & span {
                            font-weight: 400;
                            font-size: 15px;
                            color: #8a8d98;

                            &:hover {
                                color: #fff;
                            }
                        }

                        &.active {
                            background-color: #434f5c;

                            & span {
                                color: #fff;
                            }
                        }
                    }
                }

                & .tab {
                    & .container {
                        & .grid {
                            position: relative;
                            height: 90px;
                            display: flex;

                            & .header {
                                background: #e6e6e6;
                                color: #797a7b;
                                display: block;position: absolute;
                                transform: rotate(-90deg);
                                transform-origin: 0 0;
                                -webkit-transform: rotate(-90deg);
                                -webkit-transform-origin: 0 0;
                                bottom: -17px;
                                left: 0;
                                width: 90px;
                                text-align: center;
                                text-transform: uppercase;
                                white-space: nowrap;
                                overflow: hidden;
                                font-size: 14px;

                                &.second {
    
                                }
    
                                &.third {
    
                                }
                            }

                            & .list {
                                flex: 1;
                                border-bottom: 1px solid #10161d;
                                padding: 10px 0 10px 28px;
                                padding-bottom: 7px;
                                box-sizing: border-box;
                            }
                        }
                    }
                }
            }
        }

        & .trailer_iframe {
            margin-top: 5px;
            margin-bottom: 15px;
            width: 100%;
            height: 360px;
            border-radius: 5px;
            position: relative;
            max-height: 360px;
        }

        & .castlist {
            overflow: auto;
            width: 100%;
            display: flex;
            flex-wrap: nowrap;

            &::-webkit-scrollbar {
                display: none;
            }

            & .actor {
                margin-right: 15px;
                color: #78a6b8;
                font-size: 14px;
                flex: 0 0 auto;
                cursor: pointer;

                &:hover {
                    text-decoration: underline;
                }
            }
        }
    }
}