@import "../../styles/lavenst.scss";

.card {
    text-decoration: none;

    &.movie {
        width: 190px;
        height: 270.11px;
        border-radius: 5px;
        background-color: #fff;
        flex: 0 0 auto;
        margin: 0 5px;
        scroll-snap-align: start;
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        cursor: pointer;
    }

    &.app {
        display: inline-flex;
        flex-direction: column;
        align-items: center;
        margin: 0px 5px;
        cursor: pointer;

        & .icon {
            width: 32px;
            height: 32px;
            background-color: #fff;
            border-radius: 20%;
        }

        & .name {
            color: #d5d5d5;
            font-size: 13px;
            text-align: center;
            margin-top: 4px;
        }

        &.add {
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: transparent;
            border: 1px solid #576271;
            color: #576271;
            font-size: 18px;
            cursor: pointer;
            width: 32px;
            height: 32px;
        }
    }

    &.movie_title {
        display: block;
        position: relative;
        width: 100%;
        user-select: none;
        background-color: #10161d;
        border-radius: 5px;
        width: 100%;
        display: grid;
        grid-template-columns: 1.5fr minmax(0,2fr);
        grid-gap: 2rem;
        align-items: center;
        position: relative;
        
        & .poster {
            background-color :#fff;
            border-radius: 5px;
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
            padding-top: 142.1686747%;
            cursor: pointer;
            position: relative;
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;

            & .actions_group {
                width: 100%;
                height: 100%;
                position: absolute;
                top: 0;

                &::after {
                    top: 0;
                    z-index: 4;
                    content: "";
                    height: 100%;
                    width: 100%;
                    position: absolute;
                    opacity: 0;
                    pointer-events: none;
                    transition: all .3s linear;
                    display: block;
                    width: 100%;
                    height: 100%;
                    background: linear-gradient(180deg,rgba(0,0,0,.6),rgba(0,0,0,.6) 20%,transparent 45%,transparent 55%,rgba(0,0,0,.6) 80%,rgba(0,0,0,.6));
                }
    
                &:hover {
                    &::after {
                        opacity: 1;
                    }

                    & .group {
                        & .action_btn {
                            opacity: 1;
                        }
                    }

                    & .rating {
                        opacity: 1;
                    }
                }

                & .group {
                    display: flex;
                    width: 100%;
                    box-sizing: border-box;
                    padding: 0 8px;
                    z-index: 5;
                    
                    & .action_btn {
                        flex: 1;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        margin-top: 8px;
                        z-index: 5;
                        transition: all .3s linear;
                        opacity: 0;

                        &:first-child {
                            opacity: 1;
                        }

                        & .container {
                            width: 26px;
                            height: 26px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            background-color: rgba(16,22,29,.7);
                            border-radius: 50%;
                            cursor: pointer;

                            & .icon {
                                font-size: 12px;
                                color: #fff;
                            }
                        }

                        &.active {
                            & .container {
                                & .icon {
                                    color: #f4c501;
                                }
                            }
                        }
                    }
                }

                & .rating {
                    position: absolute;
                    bottom: 8px;
                    left: 10px;
                    z-index: 5;
                    opacity: 0;
                    transition: all .3s linear;
                }
            }
        }

        & .rating {
            margin-top: 18px;
            display: flex;

            & img {
                width: 16px;
                margin-right: 6px;
                vertical-align: sub;
            }

            & span {
                color: #d1d1d1;
                font-size: 14px;
            }
        }

        & .information_container {
            padding: 10px 20px 10px 0;
            height: 100%;
            box-sizing: border-box;
            position: relative;

            & .header{
                display: flex;
                margin-top: 6px;

                & .title {
                    font-weight: 700;
                    font-size: 17px;
                    color: #fff;
                    opacity: .99;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    margin: 0;
                    flex: 1;
                    text-decoration: none;

                    & .year {
                        opacity: .7;
                        font-size: 15px;
                    }
                }

                & .mark_btn {
                    width: 32px;
                    height: 32px;
                    border-radius: 50%;
                    background-color: rgba(37,41,46,.7);
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-left: 8px;
                    transition: background-color .1s ease-in-out;
                    cursor: pointer;

                    &:hover {
                        & .icon {
                            color: #8a8d98;
                        }
                    }

                    & .icon{
                        font-size: 12px;
                        color: #657182;
                    }

                    &.active {
                        background-color: #339b2a;

                        & .icon {
                            color: #fff!important;
                        }

                        &:hover {
                            background-color: #2d7527;
                        }
                    }
                }
            }

            & .overview {
                max-height: 70px;
                display: -webkit-box;
                -webkit-line-clamp: 3;
                -webkit-box-orient: vertical;
                overflow: hidden;
                text-overflow: ellipsis;
                margin: 0;
                font-size: 13px;
                color: #fff;
                opacity: .3;
                margin-top: 18px;
                line-height: 1.5;
            }

            & .watch_btn {
                width: calc(100% - 20px);
                height: 36px;
                display: flex;
                align-items: center;
                justify-content: center;
                border-radius: 4px;
                background-color: #4c5a68;
                cursor: pointer;
                position: absolute;
                bottom: 10px;
                right: 20px;
                text-decoration: none;

                &:hover {
                    background-color: #5a6774;
                }

                & span {
                    color: #fff;
                    font-size: 14px;
                }
            }
        }
    }

    &.movie_row {
        margin-bottom: 10px;
        border-bottom: 1px solid #10161d;
        display: flex;
        width: 100%;

        & .poster {
            width: 189px;
            height: 289px;
            background-color: #fff;
            border-radius: 5px;
            box-sizing: border-box;
            margin: 10px 10px 10px 0;
            background-size: cover;
            background-position: center center;
            background-repeat: no-repeat;
        }

        & .information_container {
            flex: 1;
            box-sizing: border-box;
            padding: 10px;

            & .title {
                font-size: 22px;
                color: #fff;
                margin-right: 6px;
                text-decoration: none;

                & .year {
                    font-size: 16px;
                    color: #8a8d98;
                    text-shadow: 0 1px 0 #000;
                }
            }

            & .subtitle {
                font-weight: 700!important;
                text-transform: uppercase;
                margin-bottom: 5px;
                font-size: 16px;
                margin-top: 24px;
                color: #4c5a68;
                display: block;
            }
        }

        & .apps_supported_group {
            display: flex;
            overflow: auto;

            & .col {
                margin-right: 10px;
                padding: 10px 0;

                & .header {
                    background: #e6e6e6;
                    color: #797a7b;
                    font-size: 14px;
                    height: 20px;
                    padding: 0 10px;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    &.second {
                        background: #797a7b;
                        color: #d5d5d5;
                    }

                    &.third {    
                        background: #1c252f;
                        color: #d5d5d5;
                    }
                }

                & .list {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-top: 10px;
                }
            }
        }

        & .rating {
            margin-top: 18px;
            display: flex;

            & img {
                width: 16px;
                margin-right: 6px;
                vertical-align: sub;
            }

            & span {
                color: #d1d1d1;
                font-size: 14px;
            }
        }
    }
}