@import "../../styles/lavenst.scss";

nav.navigation_bar {
    width: 100%;
    height: 56px;
    position: fixed;
    box-sizing: border-box;
    top: 0;
    left: 0;
    background-color: rgba(6,13,23,.8);
    z-index: 999;
    display: flex;
    align-items: center;
    justify-content: center;
    backdrop-filter: blur(20px) saturate(280%);
    -webkit-backdrop-filter: blur(20px) saturate(280%);

    & .container {
        width: 100%;
        height: 100%;
        max-width: 1365px;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0 64px;
        box-sizing: border-box;

        & .logo_container {
            color: #fff;
        }
    
        & .menu_container {
            display: flex;
            align-items: center;
            justify-content: center;

            & li {
                list-style-type: none;
                margin: 0 24px;

                & a {
                    text-decoration: none;
                    color: #999c9f;
                    font-size: 14px;

                    &:hover {
                        color: #d5d5d5!important;
                    }
                }

                &.active {
                    & a {
                        color: #d9e8ed;
                        font-weight: 700;
                    }
                }
            }
        }
    
        & .search_container {
            flex: 1;
            height: 38px;
            border-radius: 4px;
            padding: 8px;
            box-sizing: border-box;
            background-color: #00000075;
            display: flex;
            position: relative;
            align-items: center;

            & .icon {
                color: #666666;
                margin-right:6px;
                margin-left: 2px;
            }

            & .search_input {
                flex: 1;
                border: none;
                background: transparent;
                outline: none;
                height: 100%;
                width: 100%;
                color: #fff;
                font-size: 15px;
                margin-top: -2px;
            }

            & .search_suggestion {
                z-index: 200;
                border-radius: 0 0 10px 10px;
                min-height: 226px;
                max-height: 500px;
                width: 100%;
                transition: height .1s ease-out;
                background-color: #10161d;
                display: flex;
                flex-direction: column;
                position: absolute;
                padding: 24px;
                box-sizing: border-box;
                top: 38px;
                left: 0;
                visibility: hidden;

                &.show {
                    visibility: visible;
                }

                & .loading_container {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    height: 128px;
                }

                & .recent_container {
                    padding: 0 10px;
                    & .header {
                        display: flex;
                        flex-direction: row;
                        position: relative;

                        & .title {
                            font-size: 16px;
                            font-weight: 500;
                            letter-spacing: .49px;
                            line-height: 22px;
                            color: #797a7c;
                        }

                        & .action_btn {
                            color: #78a6b8;
                            cursor: pointer;
                            font-size: 16px;
                            font-weight: 600;
                            letter-spacing: .49px;
                            line-height: 22px;
                            position: absolute;
                            right: 0;

                            &:hover {
                                color: #d9e8ed;
                            }
                        }
                    }

                    & .list {
                        display: flex;
                        margin-top: 20px;
                        flex-wrap: wrap;

                        & .item {
                            border-radius: 8px;
                            border: 1px solid #383d47;
                            white-space: nowrap;
                            overflow: hidden;
                            text-overflow: ellipsis;
                            color: #797a7b;
                            margin-right: 8px;
                            margin-bottom: 12px!important;
                            cursor: pointer;
                            font-size: 15px;
                            padding: 0 10px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            height: 31px;

                            & .icon {
                                font-size: 16px;
                                color: #8a8d98;
                            }

                            & span {
                                color: #fff;
                                font-size: 13px;
                                font-weight: 500;
                            }

                            &:hover {
                                border: 1px solid #5e6b76;
                            }
                        }
                    }
                }

                & .suggest_container {
                    & .result_container {
                        display: flex;
                        
                        & .col {
                            flex: 1;
                            padding: 0 15px;

                            & .header {
                                display: flex;
                                border-bottom: 1px solid #383d47;
                                height: 45px;
                                align-items: center;

                                & .title {
                                    color: #8a8d98;
                                    font-size: 15px;
                                    font-weight: 700;
                                    letter-spacing: 1.36px;
                                }
                            }

                            & .list {
                                display: flex;
                                flex-direction: column;
                                margin-top: 10px;

                                & .item {
                                    display: flex;
                                    align-items: center;
                                    border-radius: 7px;
                                    margin: 0 0 16px 0;
                                    cursor: pointer;

                                    &:hover {    
                                        background: #1c252f;
                                    }

                                    & .poster_container {
                                        height: 75px;
                                        width: 56px;
                                        margin-right: 16px;
                                        padding: 5px;
                                        flex-shrink: 0;
                                        box-sizing: border-box;

                                        & .poster {
                                            position: relative;
                                            display: flex;
                                            overflow: hidden;
                                            padding-top: 142.1686747%;
                                            background-color: #fff;
                                            transition: opacity .1s ease;
                                            border-radius: 5px;
                                            background-position: center center;
                                            background-size: cover;
                                            background-repeat: no-repeat;
                                        }
                                    }

                                    & .avatar_container {
                                        height: 75px;
                                        width: 75px;
                                        margin-right: 11px;
                                        padding: 10px;
                                        margin-left: -2px;
                                        box-sizing: border-box;

                                        & .avatar {
                                            width: 100%;
                                            height: 100%;
                                            border-radius: 50%;
                                            background-position: center center;
                                            background-size: cover;
                                            background-repeat: no-repeat;
                                        }
                                    }

                                    & .information_container {
                                        flex: 1;
                                        display: flex;
                                        justify-content: center;
                                        flex-direction: column;

                                        & .title {
                                            color: #fff;
                                            font-size: 18px!important;
                                            font-weight: 600;
                                            letter-spacing: .27px;
                                            line-height: 28px;
                                            margin: 0;
                                        }

                                        & .year {
                                            font-weight: 400;
                                            letter-spacing: .4px;
                                            line-height: 22px;
                                            color: #8a8d98;
                                            font-size: 13.33333px;
                                        }
                                    }
                                }
                            }
                        }
                    }

                    & .foot {
                        color: #78a6b8;
                        background-color: #10161d;
                        display: flex;
                        justify-content: center;
                        font-size: 17px;
                        font-weight: 700;
                        padding: 15px;
                        letter-spacing: .3px;
                        line-height: 28px;
                        text-align: center;
                        border-radius: 0 0 10px 10px;
                        border-top: .3px solid #1c252f;
                        cursor: pointer;
                        margin-bottom: -24px;

                        &:hover {
                            & span {
                                color: #d9e8ed;
                            }
                        }
                    }
                }
            }
        }

        & .search_backdrop {
            background-color: rgba(0,0,0,.5);
            position: absolute;
            z-index: -1;
            width: 100vw;
            height: 100vh;
            top: 0;
            left: 0;
            visibility: hidden;

            &.show {
                visibility: visible;
            }
        }
    
        & .actions_container{
    
        }
    }
}