.selectbox {
    z-index: 99;

    & .backdrop {
        z-index: -1;
        background-color:rgba(0, 0, 0, 0.4);
        width: 100vw;
        height: 100vh;
        position: absolute;
        top: 0;
        left:0;
        opacity: 0;
        visibility: hidden;

        &.show {
            opacity: 1;
            visibility: visible;
        }
    }

    & span.label {
        color: #8a8d98;
        margin: 8px 8px 8px 0;
        font-size: 14px;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;

        &:hover {
            color: #d5d5d5!important;
        }

        & .icon {
            margin-top: 2px;
            margin-left: 4px;
        }
    } 

    & .popup {
        width: 250px;
        padding: 8px 0;
        box-sizing: border-box;
        background-color: #050d17;
        position: absolute;
        opacity: 0;
        visibility: hidden;

        &.show {
            opacity: 1;
            visibility: visible;
        }

        & .item {
            width: 100%;
            height: 48px;
            display: flex;
            align-items: center;
            font-size: 16px;
            color: #8a8d98;
            cursor: pointer;
            padding: 0 16px;
            box-sizing: border-box;

            &.active {
                color: #fbc500;
            }

            &:hover  {
                color: #fff;
                background-color: #070f19;
            }
        }
    }
}